@use 'base/variables' as v;
@use 'base/mixins' as m;

.tabs {

    @include m.tablet {
        display: flex;
    }
    
    button {
        color: v.$blanco;
        border: none;
        background-color: v.$rosadoOscuro;
        padding: 1rem 2rem;
        text-transform: uppercase;
        font-weight: v.$bold;
        transition: background-color 300ms ease;

        display: block;
        width: 100%;
        margin-bottom: 1rem;

        &:hover {
            cursor: pointer;
            background-color: darken(v.$rosadoOscuro, 10%);
        }

        &.actual {
            background-color: v.$blanco;
            color: v.$rosadoOscuro;
        }
    }

}