@use 'base/variables' as v;
@use 'base/mixins' as m;

.contenido-resumen {

    span {
        font-weight: v.$bold;
        font-size: 2.4rem;
    }

}

.contenedor-servicio {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: v.$blanco;
    padding: 0 0 0 1.5rem;
    margin-bottom: 1rem;
    border-radius: .5rem;
    height: 5rem;

    p {
        color: v.$negro;
    }

    .contenedor-precio {
        justify-content: center;
        background-color: v.$rosadoOscuro;
        border-top-right-radius: .5rem;
        border-bottom-right-radius: .5rem;
        padding: 1rem 3rem;
        width: 25%;
        height: 5rem;
        display: flex;
        align-items: center;

        span {
            color: v.$blanco;
        }

    }

}

h3:last-of-type {
    margin-top: 5rem;
}

.contenedor-datos {
    padding: 1rem;
    border-radius: .5rem;

    span {
        color: v.$rosadoOscuro;
    }
}