@use 'base/variables' as v;

.seccion {
    padding: 5rem 0 0;
    display: none;

    .text-center {
        text-align: center;
    }
}

.barra {

    display: flex;
    justify-content: space-between;

    span {
        color: v.$blanco;
        text-transform: uppercase;
        font-weight: v.$bold;
    }

    p {
        color: v.$rosadoOscuro;
        margin-top: 0;
    }

    .boton {
        margin-top: 0;
        border-radius: 1rem;
        padding: .6rem;
        font-size: 1.8rem;
        width: 16rem;
        background-color: transparent;
        font-weight: v.$regular;
    }

    .boton i{
        color: v.$rosadoOscuro;
    }

    .boton:hover i{
        color: v.$blanco;
    }


}

.mostrar {
    display: block;
}

.ocultar {
    display: none;
}