@use 'base/variables' as v;
@use 'base/mixins' as m;

.contenedor-app {
    @include m.tablet {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        min-height: 100vh;
    }

    .imagen {
        height: 20rem;
        background-image: url(../img/5.jpg);
        background-size: cover;
        background-position: center center;

        @include m.tablet {
            height: auto;
        }
    }

    .app {
        width: 95%;
        margin: 0 auto;
        padding: 3rem 0;

        @include m.tablet {
            overflow: scroll;
            overflow-x: hidden;
            margin-right: 0;
            padding: 3rem 3.5rem 3rem 1rem;

            &::-webkit-scrollbar {
                width: 1rem;
                background-color: v.$blanco;
            }

            &::-webkit-scrollbar-thumb {
                background-color: v.$rosadoOscuro;
                border-radius: 10px;
            }
        }
    }
}

.barra-servicios {

    margin-bottom: 5rem;
    
    @include m.desktop {
        display: flex;
        justify-content: space-evenly;
        gap: 1.5rem;
    }
    

    .boton {
        margin: 0 0 1rem 0;
        border-radius: .5rem;
    }
}