@use 'base/variables' as v;

.citas {
    list-style: none;
    padding: 0;

    li {
        padding: 1rem;
        border-top: 1px solid v.$blanco;

        p {
            font-weight: v.$bold;
            color: v.$rosadoOscuro;
        }

        span, .servicio {
            font-weight: v.$regular;
            color: v.$blanco;
        }

        .servicio {
            background-color: darken(v.$gris, 75%);
            padding: 1.5rem .5rem;
            border-radius: .5rem;
            margin:.5rem 0;
        }
    }

    .boton-eliminar {
        margin: 3.5rem 0;
    }
}

.servicios {
    @extend .citas;

    .acciones {
        margin: 0;
        
        .boton-eliminar, .boton {
            margin: 0 0 1rem;
        }


    }
}